<!--
上传文件
weiluxia 2016.07.22
name 上传的文件参数
action 上传的地址
multiple 是否支持多选文件
data 上传所需参数
accept 接受上传的文件类型
maxSize 文件大小
fileList 已经上传的文件列表
listType 上传列表的内建样式，支持两种基本样式 text or image
onChange 上传文件改变时的状态回调(uploading done error removed)
beforeUpload 上传文件之前的钩子,返回false,则停止上传
-->
<template>
  <div class="v-upload">
    <div class="v-upload-select" @click.stop="handleClick">
      <input ref="file"
        type="file"
        :disabled="disabled"
        style="display: none"
        :accept="computed_accept"
        :multiple="multiple"
        @change="handleChange" />
      <v-button type="info" :disabled="disabled"><v-icon type="upload" />上传文件</v-button>
      <span class="v-upload-hint"><slot></slot></span>
    </div>
    <upload-list :list="computed_fileList" @del="onRemove" :list-type="listType"></upload-list>
  </div>
</template>
<script>
  import uploadList from "./UploadList.vue";
  import uploader from './uploader';
  const prefix = 'v-upload';
  export default {
    name: prefix,
    components: { uploadList },
    mixins: [ uploader ],
    props: {
      listType: {
        type: String,
        default: 'image'
      }
    }
  }
</script>


